:root {
  /* COLORS */
  --purple_1: linear-gradient(180deg, #41297D 0%, #472D88 100%);
  --purple_2: #352266;
  --purple_3: #6B43CC;
  --purple_4: #472D88;
  --white_1: #F8F8F8;
  --white_2: linear-gradient(180deg, #F7F7F7 0%, #FFFFFF 100%);
  --white_3: #FFF;
  --white_4: #F5F5F5;
  --black_1: #282828;
  --black_2: #000;
  --gray_1: #E6E6E6;
  --gray_2: #C0C0C0;
  --gray_3: #DEDEDE;
}

/* FONTS */
/* Montserrat */
*,
*:before,
*:after {
  box-sizing: border-box;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select,
  textarea,
  input {
    font-size: 1.6rem !important;
  }
}
img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

input[type=submit],
button {
  cursor: pointer;
}

hr {
  display: block;
  height: 0.1em;
  border: 0;
  border-top: 0.1em solid #ccc;
  margin: 1em 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ol {
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Slab", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
}

section,
footer,
main,
header,
.main-inner {
  width: 100%;
}

main {
  flex: 1 0 auto;
}

@media (min-width: 1200px) {
  .main-index {
    width: 70%;
    max-width: 144rem;
  }
}
@media (min-width: 1440px) {
  .main-index {
    margin-top: 8rem;
  }
}
.main-index--category {
  margin-top: 0;
}
@media (min-width: 1440px) {
  .main-index--category {
    margin-top: 0;
  }
}

footer {
  width: 100vw;
  flex: 0 0 auto;
}

.wrapper {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #EBEDEF;
}

.container {
  width: 100%;
  max-width: 144rem;
  padding: 0 1rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .container {
    padding: 0 4rem;
  }
}
.container--short {
  max-width: 90rem;
}
.container--blog {
  max-width: 86rem;
}
@media (min-width: 1024px) {
  .container--blog {
    max-width: 144rem;
  }
}
.container--no-padding {
  padding: 0;
}
@media (min-width: 1440px) {
  .container--no-padding {
    padding: 0;
  }
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 1200px) {
  .no-scroll {
    padding-right: 1.7rem;
  }
}

p {
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: var(--gray_2);
}
@media (min-width: 1440px) {
  p {
    font-size: 1.6rem;
    line-height: 2.1rem;
  }
}

h2 {
  margin-bottom: 2rem;
  font-size: 2.4rem;
  line-height: 3.2rem;
  color: var(--black_1);
}
@media (min-width: 768px) {
  h2 {
    margin-bottom: 4rem;
    font-size: 3.6rem;
    line-height: 4.7rem;
  }
}
@media (min-width: 1440px) {
  h2 {
    margin-bottom: 6rem;
  }
}

.btn {
  outline: none;
  border: none;
  transition: all 0.5s linear;
}
.btn:disabled {
  color: var(--gray_1);
}

.next,
.prev {
  width: 3rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--white_2);
  box-shadow: 0px 8px 7px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  border: 0 solid rgba(0, 0, 0, 0.5);
}
@media (min-width: 768px) {
  .next,
  .prev {
    width: 3.7rem;
    height: 3.7rem;
  }
}

.left-first,
.left-second {
  margin-right: 1.5rem;
}
@media (min-width: 768px) {
  .left-first,
  .left-second {
    margin-right: 4rem;
  }
}

.right-first,
.right-second {
  background: url("../img/icons/arrow-right.svg") no-repeat;
  background-size: 70% 70%;
  background-position: center center;
}

.left-first,
.left-second {
  background: url("../img/icons/arrow-left.svg") no-repeat;
  background-size: 70% 70%;
  background-position: center center;
}

.next.left-second.swiper-button-disabled,
.next.left-first.swiper-button-disabled {
  background: url("../img/icons/arrow-left-disabled.svg") no-repeat;
  background-size: 70% 70%;
  background-position: center center;
}

.prev.right-second.swiper-button-disabled,
.prev.right-first.swiper-button-disabled {
  background: url("../img/icons/arrow-right-disabled.svg") no-repeat;
  background-size: 70% 70%;
  background-position: center center;
}

section {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  section {
    padding: 4rem 0;
  }
}
@media (min-width: 1440px) {
  section {
    padding: 6rem 0;
  }
}

