p {
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: var(--gray_2);

  @media (min-width: 1440px) {
    font-size: 1.6rem;
    line-height: 2.1rem;
  }
}

h2 {
  margin-bottom: 2rem;
  font-size: 2.4rem;
  line-height: 3.2rem;
  color: var(--black_1);

  @media (min-width: 768px) {
    margin-bottom: 4rem;
    font-size: 3.6rem;
    line-height: 4.7rem;
  }

  @media (min-width: 1440px) {
    margin-bottom: 6rem;
  }
}